<template>
    <div>
      <tou/>
      <div id="shai">
        <button>{{this.txt}}</button>
        <button>排序</button>
        <button>筛选</button>
      </div>
      <div v-for="(item,k) in dataList" class="gaa-1" :key="k" @click="faA(item)">
      <img :src="'http://elm.cangdu.org//img/'+item.image_path" >
      <div>
        <p style="font-weight: 600;">{{item.name}}</p>
        <p style='color:#FEBE40;font-size:0.3rem'> 
          <span v-for='(ma,k) in 5' :key="k" class="el-icon-star-on"></span>
          {{item.rating}}
          <span style="color:#000">月售{{item.rating_count}}单</span>
          
          </p>
          <p>￥{{item.float_minimum_order_amount}}起送
            <span>{{item.float_delivery_fee}}</span>
            </p>
      </div>
      <div>
        <p>
           <span v-for='(val,k) in item.supports' :key="k" style='float:right'>{{val.icon_name}}</span>
        </p>
        <p>
          <span style="margin-left: 0.6rem;border:1px solid;background:#3792E3;color:#fff">{{item.delivery_mode.text}}</span>
          <span style='float:right;color:#3792E3;border:1px solid #3792E3' >{{item.supports[1].name}}</span>
        </p>
        <p>
            <span style='float:right'>{{item.distance}}/<span style="color:#3792E3">{{item.order_lead_time}}</span></span>
        </p>
      </div>
    </div>
    </div>
</template>
<script>
import tou from '../zhuye/toubiao'
export default {
    components:{
     tou,
    },
    data(){
        return{
          dataList:[],
          txt:''
        }
    },
    methods:{
        onConfirm() {
      this.$refs.item.toggle();
    },
    faA(){
      this.txt=localStorage.zhu;
          this.axios(`http://elm.cangdu.org/shopping/restaurants?latitude=39.78493&longitude=116.49476&offset=0&limit=30&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=`).then((e)=>{
         this.dataList=e.data
    })
    this.axios.get(`http://elm.cangdu.org/shopping/v2/restaurant/category?latitude=31.22299&longitude=121.36025`).then((e)=>{
      var x=[]
      for(var i in e.data ){
        var obj={};
        obj.text=e.data[i].name;
        obj.value=i
        // this.option.push(obj)
      }
      console.log(x)
    })
    }

    },
    mounted(){
        this.faA()
    }
}
</script>
<style scoped>
#shai{
  width: 100%;
  height: 1rem;
}
#shai>button{
  width:30%;
  height: 90%;
  margin:0 0.14rem;
  border: 0px;
      
}
.gaa-1{
  width: 100%;
  height: 3rem;
  border-bottom: 1px solid #999;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  
}
.gaa-1>img{
  width: 2rem;
  height: 2rem;
}
.gaa-1>div{
  width: 4rem;
  height: 100%;
  margin-left: 0.5rem;
  display: flex;
  flex-wrap: wrap;
  padding-top:0.3rem; 
  box-sizing: border-box;
}
.gaa-1>div>p{
  width: 100%;
  height: 0.6rem;
  box-sizing: border-box;
  line-height: 0.6rem;
  overflow: auto;
}
</style>


